.trending-slick-container{
    margin-top: 950px;
    max-width: 2000px;
}


.trending-col-container{
    display: grid;
    grid-column: 1;
    grid-row: 1;
    margin-left: 30px;
    width: 461px;
}

.slick-container{
    position: absolute;
    left: 45%;
    margin-top: -60%;
}
.slick__item{
    padding: 0px;
    height: 420px;
    min-width:250px;
}

.mybtn_prev{
    left: -30px;
    top: -79%;
    
}

.mybtn_next{
    top: 22%;
    left: 580px;  
}


.trending-btn{
    &__b{
        margin-top: 10px;
    }
}

