.discount-container-grid{
    min-height: 298px;
    grid-template-columns: repeat(auto-fit, minmax(125px, 256px));
    grid-gap: 28px;
   
}
.discount-container-text{
   
    &__take{
        @include font-style (24px, $grey);
    }
    &__h3{
        @include font-style (60px, $fontBlack);
    }
    &__p{
        @include font-style (18px, $fontBlack);
    }
}