
.slider-container{
    max-width: 2000px;
    margin: auto;
    position: relative;
    margin-top: 101px;
}

.dots-box{
    margin-top: 0px;
}
.myarrow{
    margin: 0;
    padding: 0;
    position: absolute;
    top: 40%;
    z-index: 10;
    transition: all .3ms linear;
    cursor: pointer;
}
.myarrow_prev{
    padding: 2px 4px;
    @include btn ();
    cursor: pointer;
    opacity: 0;
    left: 30px;
}

.myarrow_next{
    padding: 2px 4px;
    @include btn ();
    cursor: pointer;
    opacity: 0;
    left: 900px;
}

.myarrow_next:hover{
    @include btn-hover ();
    @include border();
    --fill-color: #A6866A;
}

.myarrow_prev:hover{
    @include btn-hover ();
    @include border();
    --fill-color: #A6866A;
}
 
.slider-container:hover .myarrow_prev{
    opacity: unset;
}

.slider-container:hover .myarrow_next{
    opacity: unset;
}

.slider-text{
    top: 120px;
    margin-left: 280px;

    &__title{
        @include font-style (32px, $fontBlack);
    }
    &__p{
        @include font-style (15px, $fontBlack);
      
      
    }
}

.slider-text-btn{
    &__btn{
        @include btn ();
        padding: 10px 20px;
        cursor: pointer;
    }
    &__btn:hover{
        @include btn-hover ();
        @include border();
    }
}