.container-first{

    margin-top: 4px;
    letter-spacing: 1px;
   
    &__p{
        font-size: 14px;
    }
    &__par{
        font-size: 26px;
        margin-top:-5px;
    }
    &__nav{
        padding: 4px 10px;
        &_link{
            font-size: 15px;
            padding: 2px 5px;
            
        }
    }
   
}

.grid-container{
    min-height: 321px;
    grid-template-columns:repeat(auto-fit, minmax(125px, 159px));
    grid-column-gap: 10px;
  
    &__img {
        height: 198px;
     
    }
    &__p, &__par{
        @include font-style (13px, $fontBlack);
    }
   
}
