.footer-container{
    margin-bottom: 40px;
}

.footer-grid{
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(148px, 286px));
    justify-content: center;
    justify-items: center;
}
.footer-nav{
 margin: 0;
 padding: 0;
    &__list{
        text-align: start;
        justify-content: start;
        justify-items: start;
        padding-left: 55px;
        &_title{
            @include font-style (18px, $fontBlack);
            font-weight: 500;
            text-align: start;
            justify-content: start;
            justify-items: start;
            margin-top: 10px;
        }
        &_link{
            text-decoration: none;
            @include font-style (16px, $lightGrey);
            text-align: start;
            justify-content: start;
            justify-items: start;
            margin-top: 8px;
        }
    }
} 
.footer-svg-box{
    display: inline-block;
     &__link{
         text-decoration: none;
         padding: 4px;
         justify-self: normal;
     }
 }
.form-control{
    padding: 15px 260px 15px 15px;
}
.form-btn{
    display: block;
    margin-left: 381px
}
 


