.footer-grid{
    grid-template-columns: repeat(auto-fit, minmax(148px, 290px));
}
.footer-nav{
    text-align: justify;
    &__p{
        @include font-style (16px, $lightGrey);
        justify-self: stretch;
        line-height: 1.5;
        margin: 0;
        padding: 0px;
        
    }
       &__list{
           
           &_title{
               margin-top: 25px;
           }

           &_link{
               margin-top: 8px;
           }
       }
   } 
.form-control{
    padding: 15px 320px 15px 15px;
}
.form-btn{
   display: none;
}
.form-wrap-btn{
    margin-top: -50.5px;
    margin-left: 364px;
    &__btn{
        opacity: unset;
        @include btn ();
        text-transform: uppercase;
        padding: 18px 20px;
        font-weight: 500;
        letter-spacing: 1.5px;
       
    }
}

