.slick-container{
    margin: auto;
    position: relative;
    max-width: 580px;
   
}

.slick__item{
    padding: 0px;
    max-height: 350px;
    max-width: 264px;
}

.slick__img{
    width: 100%;
    max-width: 100%;
   
    vertical-align: top;
    position: relative;
    box-shadow: 0px 0px 25px rgba(112, 111, 112, 0.06);
}

.slick-svg-box{
    margin-top: 10px;
    text-align: center;
}

.slick{
    &__btn{
        margin-top: 10px;
        text-align: center;
        &_b{
            @include btn (13px);
            padding: 10px 20px;
            text-transform: uppercase;
            letter-spacing: 0.6px;
            text-align: center;
        }
    }

    &__dscr{
        text-align: center;
        letter-spacing: 0.6px;
        &_p{
            @include font-style (14px, $fontBlack);
        }
        &_price{
            @include font-style (14px, $fontBlack);
            font-weight: 500;
        }
        &_span{
            text-decoration: line-through;
            color: $grey;
        }
    }
}

.trending-btn{
    text-align: center;
    margin-top: 30px;
    margin-bottom: 20px;
    &__b{
        @include btn ();
        padding: 10px 20px;
        text-transform: uppercase;
    }
   
}