.container-first{
    text-align: center;
    max-width: 3000px;
    margin-top: 0px;
    letter-spacing: 1.5px;
   
    &__p{
        font-size: 14px;
        color: $grey;
        text-align: center;
    }
    &__par{
        font-size: 24px;
        color: $black;
        text-align: center;
        margin-top:-5px;
    }
    &__nav{
        display: flex;
       flex-direction: column;
        text-align: center;
        padding: 4px 10px;
        
        &_link{
            text-decoration: none;
            color: $grey;
            font-size: 15px;
            padding: 2px 4px;
        }
    }
}

.grid-container{
    min-height: 294px;
    display: grid;
    grid-template-columns:repeat(auto-fit, minmax(123px, 125px));
    grid-column-gap: 20px;
    justify-content: center;
    margin-top: 16px;
    
    &__img {
        width: 100%;
        height: 161px;
        box-shadow: 0px 0px 25px rgba(112, 111, 112, 0.08);
      
    }
    &__p, &__par{
        @include font-style (14px, $fontBlack);
    }
    &__par{
        &_2, &_3, &_4{
            text-decoration: line-through;
            color: $grey;
        }
    }

    &__svg{
        margin-top: 15px;
    }
    &__btn{
        @include btn (13px);
        padding: 8px 14px;
        margin-top: 10px;
    }
   
   
}

.button-box{
 margin-top: 22px;

    &__btn2{
        @include btn ();
        padding: 10px 20px;
        margin-top: 15px;
        font-family: $baseFontFamily;
        
    }
    &__btn1{
        @include btn ();
        padding: 4px 6px;
        margin: 0;
        font-family: $baseFontFamily;
    
    }
}

