.header-container{
    position: fixed;
    z-index: 10000px;
    background: $header;
    box-shadow: 0px 0px 25px rgba(43, 15, 64, 0.09);
    top: -15px;

}

.icon-box{
    background: $white;
   
}

.header-info-box{
    min-height: 20px;
    display: flex;
    justify-content: center;
    justify-items: center;
  

    &__text, &__link, &__link2{
        display: none;
    }
    &__list{
        list-style: none;
    }
    &__select-control{
        @include font-style(14px, $white);
        font-weight: 400;
        letter-spacing: 1.5px;
        justify-self: center;
        margin: 10px 2px auto 5px;
        padding: 5px;
       
        &_1{
            @include font-style(14px, $white);
            font-weight: 400;
            letter-spacing: 1.5px;
            padding-left: 10px;
            border-left: 1px solid $white;
            justify-self: center;
            margin: 10px 5px auto 5px;
        }
    }   
}

.select-control option{
    @include font-style (16px, $dark);
}




