.icon-box-container{
    margin: 0;
    padding:0;
 
}

.icon-box-nav{
    margin: 0;
    margin-top: 25px;
    padding:0 10px;
    display: table-cell;
    vertical-align: middle;
  

    &__link{
        @include font-style (14px, $fontBlack);
        padding: 0;
        margin: 0;
        padding: 10px;
        transition: 0.3s;
    }

    &__link:hover{
       cursor: pointer;
       color: $brown;
       border-bottom: 1px solid $brown;
    }
   
    &__logo{
        @include font-style (28px, $black);
        margin: 0;
        padding: 0;
        text-align: center;
        margin-top: -10px;
        margin-left: 20px;
    }

    &__list{
        list-style: none;
        margin: 0;
        padding:0;
        display: table-cell;
        vertical-align: middle;
    }
    &__li, &__link, &__cart{
        text-decoration: none;
        display: table-cell;
        vertical-align: middle;
        margin: 0;
        padding: 0;
        padding:0 3px;
    }
      &__user, &__bag,  &__search, &__cart{
        display: none;
        margin: 0;
        padding: 0;
    }
}

.icon-box-btns{
    margin: 0;
    padding: 0;
    display: flex;
    vertical-align: middle;
    margin-top:0px;
    margin-left: 40px;
   
    &__link{
        text-decoration: none;
        @include font-style (14px, $fontBlack);
        margin: 0;
        padding: 0px;
        vertical-align: middle;
        margin-left: -30px;
        &_cart{
            text-decoration: none;
            @include font-style (14px, $fontBlack);
            margin: 0;
            padding: 0px;
            vertical-align: middle;
            margin-left: -30px;
        }
    }

    &__link:hover{
        cursor: pointer;
        color: $brown;
        border-bottom: 1px solid $brown;
    }
    &__link{
        &_cart:hover{
            cursor: pointer;
            color: $brown;
            border-bottom: 1px solid $brown;
        }
    }

    &__search, &__cart{
        margin: 0;
        padding: 0;
        vertical-align: middle;
    }

    &__search, &__cart:hover{
        cursor: pointer;
    }

}

.dark-overlay {
    display: none;
}
.mobile-menu {
    display: none;
}
.menu-button {
    display: none;
}
.menu {
    display: block;
}
.menu ul {
    list-style: none;
    padding: 0;
    margin: 0;
    
}
.menu li {
    position: relative;
    
}

/* 1st level. */
.menu > ul > li {
    display: inline-block;
    @include font-style (13px, $fontGray);
    padding: 2px;
}

.menu > ul > li > a {
    display: inline-block;
    padding: 2px;
}

/* 2nd level. */
.menu li ul {
    opacity: 0;
    visibility: hidden;
    position: absolute;
    top: 100%;
    left: 0;
    width: 15rem;
    padding: 1.5rem 0;
    background: $white;
    transition: all 200ms ease;
    z-index: 10000;
    border: 1px solid $border;
}

.menu li:hover > ul {
    opacity: 1;
    visibility: visible;
}

.menu li ul a {
    display: block;
    padding: 8px;
}

/* 3rd level. */
.menu li ul li ul {
    top: -1.5rem;
    left: 100%;
    z-index: 9000;
}

/* Drop-down arrows. */
.menu > ul > li.has-submenu:after,
.menu li ul li.has-submenu:after {
    content: "";
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    width: 0;
    height: 0;
}

/* 1st level arrows. */
.menu > ul > li.has-submenu:after {
    right: 5px;
    border-left: 5px solid transparent;
    border-right: 5px solid transparent;
    border-top: 5px solid rgba(0, 0, 0, .25);
}

/* 2nd level arrows. */
.menu li ul li.has-submenu:after {
    right: 1.5rem;
    border-top: 5px solid transparent;
    border-bottom: 5px solid transparent;
    border-left: 5px solid rgba(0, 0, 0, .25);
}



