.footer-container{
    margin: 0px;
    padding: 0;
    margin-bottom: 20px;
    margin-left: 20px;
}

.footer-grid{

    grid-template-columns: repeat(auto-fit, minmax(110px, 190px));
    vertical-align: top;
    grid-gap: 0px;
    justify-content: start;
    justify-items: start;
}


.footer-nav{
    text-align: start;
    vertical-align: top;
    &__title{
        @include font-style (20px, $fontBlack);
        text-align: start;
        font-weight: 500;
        letter-spacing: 1.5px; 
        margin: 0;
        padding: 0;
        margin-top: 0px;
        
    }

    &__p{
        @include font-style (14px, $lightGrey);
        justify-self: start;
        line-height: 1.5;
        margin: 0;
        padding: 0;
    }

    &__list{
        line-height: 0;
        vertical-align: text-top;
        justify-items: start;
        margin: 0;
        padding: 0;
       
        &_link{
            @include font-style (14px, $lightGrey);
            justify-items: start;
            margin: 0;
            padding: 0;
            padding-top: -165px;
        }

        &_link:hover{
            color: $brown;
        }
    }
} 
.label-control{
    margin-right:0px;
    margin-left: 0px;
    margin-top: 0px;
}
.form-control{
    padding: 15px 25px 15px 15px;
}
.form-control:hover{
    border: 1px solid $header;
}

.form-wrap-btn{
    margin-top: 15px;
    margin-left: 0px;
    &__btn{
        padding: 15px 20px;
        transition: 0.3s;
    }
    &__btn:hover{
        @include btn-hover ();
        @include border();
        cursor: pointer;
    }
}

.footer-svg-box{
    
    &__link:hover{
        cursor: pointer;
        --fill-color: #232323;
    }
 }

.list-item-1{
    display: grid;
    grid-column: 1;
    grid-row: 1;
    margin-left: 0px;
    margin-top: 0px;
}

.list-item-2{
    display: grid;
    grid-column: 5;
    grid-row: 1;
    margin-left: 0px;
    margin-top: 10px;
}

.list-item-3{
    display: grid;
    grid-column: 2;
    grid-row: 1;
    margin-left: 40px;
}

.list-item-4{
    display: grid;
    grid-column: 3;
    grid-row: 1;
    margin-left: 30px;
}

.list-item-5{
    display: grid;
    grid-column: 4;
    grid-row: 1;
    margin-left: 30px;
}


.footerline-container{
    margin-right: 10px;

} 
