.latest-container{
    margin: 0;
    padding: 0;
    max-width: 2000px;
    margin-top: 40px;
    
}
.latest-grid{
    min-height: 248px;
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(288px, 290px));
    justify-content: center;
    grid-column-gap: 0px;
    grid-row-gap: 0;
    
   
    &__1{
        justify-content: center;
      &_img{
        width: 100%;
        justify-self: center;
      }
    }

    &__2{
        justify-content: center;
        justify-items: center;
        background: $backBeige;
        text-align: center;
        margin: 0;
        padding: 0;
       

        &_c{
            @include font-style (14px, $grey);
            text-transform: lowercase;
            letter-spacing: 3.5px;
        }
        &_h{
            @include font-style (26px, $black);
            font-weight: 400;
            letter-spacing: 0.8px;
        }
        &_p{
            @include font-style (14px, $lightGrey);
            letter-spacing: 1.2px;
            padding: 8px 8px;
        }
        &_btn{
            @include btn ();
            padding: 10px 20px;
            margin-bottom: 20px;
            text-transform: uppercase;
        }
    }
}

