
.lamp-grid{
    display: grid;
    grid-gap: 34px;
    grid-template-columns: repeat(auto-fit, minmax(280px, 288px));
    justify-content: center;
    justify-items: center;
    padding: 0;
    margin-top: 30px;
}   
.col{
    &__img-left, &__img-right{
        display: grid;
        object-fit: cover;
        width: 100%;
    }

    &__about-text-block{
        letter-spacing: 0.8px;
        display: grid;
        text-align: center;
        margin: 0;
        padding: 0;
       
        &_title{
            @include font-style (26px, $black);
         
        }
    
        &_description{
            @include font-style (14px, $lightGrey);
            margin-top: 0;
           
        }
    }

    
}
    
.col-about-btn{
    display: grid;
    justify-content: center;
    margin-top: 20px;
    
    &__buttn {
        @include btn ();
        padding: 10px 20px;
        text-transform: uppercase;
        letter-spacing: 0.5px;
        margin: 0;
    }
}
   

   
    

