.trending-slick-container{
    margin-top: 840px;
    max-width: 320px;
    margin-bottom: 60px;
}
.trending-col-container{
    display: grid;
    grid-column: 1;
    grid-row: 1;
   margin-left: -330px;
    width: 400px;
}
.slick-container{
    position: absolute;
    left: 41%;
    margin-top: -70%;
    grid-gap: 2px;
  
}

.slick__item{
    padding: 0px;
    height: 390px;
    min-width: 100px;
}
.slick{

    &__dscr{
        &_price{
            @include font-style (16px, $fontBlack);
        } 
    }

    &__btn{
        
        &_b:hover{
            @include btn-hover ();
            @include border();
            cursor: pointer;
        }
    }
    &__btn{
        
        &_b{
            transition: .3s linear;
        }
    }
}
.mybtn{
   cursor: pointer;
  
}
.mybtn_prev{
    left: 0px;
    top: -75%;
    
}

.mybtn_next{
    top: 25%;
    left: 540px;  
}


.trending-btn{
    &__b{
        margin-top: 10px;
        cursor: pointer;
        transition: .3s linear;
    }

    &__b:hover{
        @include btn-hover ();
        @include border();
        overflow: hidden;
    }
   
}

