.trending-slick-container{
    margin-top: 1720px;
    max-width: 680px;
}
.trending-col{

    &__btn{
        display: none;
    }
    &__dscr{
        &_p{
            @include font-style (18px, $fontBlack);
        }
        &_price{
            @include font-style (18px, $fontBlack);
    
        }
    }
}
.trending-desc{
  
    &__title{
     
        @include font-style (30px, $black);

    }
    
}
.slick__item{
    padding: 5px;
    height: 390px;
    min-width: 280px;
}
.slick{
    &__dscr{

        &_price{
            @include font-style (16px, $fontBlack);
        } 
    }
}
.mybtn_prev{
    left: -7px;
  top: -70%;
    
}

.mybtn_next{
    top: 26.5%;
    left: 550px;  
}


.trending-btn{
    &__b{
        margin-top: 10px;
        padding: 12px 22px;
    }
   
}

