.wrap{
  position: relative;
  z-index: 10000;
}

.header-container{
  max-width: 2000px;
  width: 100%;
  min-height: 1vh;
  margin: 0;
  padding: 0;
}

.slider-box{
  padding: 0;
  margin: 0;
  max-width: 100%;
  width: 100%;
  position: absolute;
  z-index: 9999;
}

// .text-block-bg{
