//   background: $white;
//   background-size: 100%;
// }

.text-block-wrap{
  position: relative;
  z-index: 2;
}



.container {
  max-width: 2000px;
  margin: auto;
  padding: 0 12px;
}

.container-lamp-bg{
  display: block;
  position: absolute;
  width: 100%;
  height: 1120px;
  left: 0;
  background: $beige;
  background-size:100%;
}

.text-img-wrap{
  position: relative;
  z-index: 2;
}

.discount-container-bg{
  display: block;
  position: absolute;
  width: 100%;
  height: 630px;
  left: 0;
  background: $beige;
  background-size:100%;
}

.discount-container-wrap{
  position: relative;
  z-index: 2;
}

.footer-bg{
  display: block;
  position: absolute;
  width: 100%;
  height: 1310px;
  left: 0;
  background: $white;
  background-size:100%;
  max-width: 3000px;
}

.footer-bg-line{
  display: block;
  position: absolute;
  width: 100%;
  left: 0;
  background: $header;
  background-size:100%;
  max-width: 2000px;
}

.footer-wrap{
  position: relative;
  z-index: 2;
  padding: 0;
  margin: 0;
  max-width: 3000px;
}