.blog{
    max-width: 2000px;
    margin: 0;
    padding: 0;
    margin-top: 40px;
    border-bottom: 1px solid $border;
 

    &__desc{
        @include font-style (14px, $grey);
        letter-spacing: 2.5px;
        text-align: center;
    }
    &__title{
        @include font-style (26px, $black);
        font-weight: 400;
        letter-spacing: 0.8px;
        text-align: center;
        margin: 0;
        padding: 0;
    }
}
.blog-slide-box{
    min-height: 196px;
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(288px,290px));
    justify-content: center;
    justify-items: center;
    grid-gap: 20px;
    margin-top: 10px;
    margin-bottom: 40px;

    &__f{
        &_h{
            @include font-style (16px, $fontBlack);
            font-weight: 400;
        }
        &_p, &_link{
            @include font-style (14px, $linkColor);
            font-weight: 400;
        }
        &_link{
            text-decoration: none;
            border-bottom: 1px solid $linkColor;
        }
    }
}

.slide-img{
    display: grid;
    width: 100%;
    justify-self: center;
}