.icon-box-container{
    max-width: 2000;
    width: 100%;
    display: flex;
    justify-items: center;
    justify-items: center;
    z-index: 9999px;

}
.icon-box-nav{
    display: flex;
    justify-items: center;
    justify-items: center;
    margin: 0;
    margin-top: 8px;
    padding:0 4px;
    display: inline-block;
    text-align: center;
    vertical-align: top;
 
    &__list{
        list-style: none;
        margin: 0;
        padding:0;
    }
   
    &__logo{
        @include font-style (22px, $black);
        letter-spacing: 3px;
        font-weight: 600;
        text-align: center;
        justify-content: center;
        justify-items: center;
        margin: 0;
        padding: 0;
       
    
    }

     &__search, &__logo, &__user, &__user, &__bag{
        margin: 0px;
        padding: 0px;
 
    }
    &__search{
        margin-left: -13px;
    }

    &__li, &__link, &__cart{
        text-decoration: none;
        display: none;
        &_search, &_cart{
            display: none; 
        }
    }
}
.icon-box-btns{
    display: none;
}




// ==========================================================================
// ============================================================================


.navbar {
    padding: 1rem;
    padding-left: .5rem;
    display: flex;
    justify-content: space-between;
    align-items: center;
    z-index: 10000;
}

.navbar a {
    text-decoration: none;
    color: $grey;
    transition: color 200ms ease-in;
    cursor: pointer;
}

.navbar li:hover > a {
    color: $fontBlack;
}

.menu {
    display: none;
}


/** ---------------------------------------------------------------------------
 * Menu button.
 * ------------------------------------------------------------------------- */
/*#region*/ 
.menu-button {
    display: inline-block;
	transition: left .3s;
	width: 25px;
	height: 25px;
    position: relative;
    top: 5px;
}
.menu-button.in-mobile {
    position: absolute;
    top: 2rem;
    right: 1rem;
}

.menu-button span {
    position: absolute;
    height: 2px;
    border-radius: 10px;
    width: 100%;
    background-color: #3D4146;
    // top: 150px;
    transition: transform .2s ease-in-out, opacity .2s ease-in-out;
}

.menu-button span:nth-child(1) {
    top: 0;
}

.menu-button span:nth-child(2),
.menu-button span:nth-child(3) {
    top: 7px;
}

.menu-button span:nth-child(4) {
    top: 14px;
}

.menu-button.active span:nth-child(1),
.menu-button.active span:nth-child(4) {
    opacity: 0;
}

.menu-button.active span:nth-child(2) {
    transform: rotate(45deg);
}

.menu-button.active span:nth-child(3) {
    transform: rotate(-45deg);
}
/*#endregion*/

/** ---------------------------------------------------------------------------
 * Mobile menu 
 * ------------------------------------------------------------------------- */

/*#region*/

 /* Dark overlay.  */
.dark-overlay {
    position: fixed;
    top: 0;
    left: 0;
    visibility: hidden;
    z-index: 99998;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, .8);
    opacity: 0;
    transition: opacity .3s, visibility .3s;
}

.dark-overlay.visible {
    opacity: 1;
    visibility: visible;
}

/* Mobile menu. */
.mobile-menu {
    background-color: #fff;
    padding: 6rem 0 2rem;
	position: fixed;
    width: 20rem;
	top: 0;
	right: -100%;
	transition: left .3s ease-in;
    z-index: 99999;
    height: 100%;
    overflow: scroll;
}

.mobile-menu.visible {
    right: 0;
}

.mobile-menu ul {
    list-style-type: none;
    padding: 0;
    margin: 0;
}

.mobile-navigation a {
    display: block;
    padding: 1rem;
    color: $fontBlack;
    text-decoration: none;
    transition: all 1s;
}

.mobile-navigation a.active {
    background-color: rgba(0, 0, 0, .1);
}

.mobile-navigation li {
    position: relative;
}

/* 1st level. */
.mobile-navigation > ul > li > a {
    // font-weight: bold;
    color: $grey;
}

/* Lower levels. */
.mobile-navigation li ul {
    padding-left: 2rem;
    display: none;
}
            
/* Drop-down arrows. */
.mobile-navigation li.has-submenu:after {
    content: "";
    position: absolute;
    top: 1.6rem;
    width: 0;
    height: 0;
}

.mobile-navigation li.has-submenu:after {
    right: 1rem;
    border-left: 5px solid transparent;
    border-right: 5px solid transparent;
    border-top: 5px solid rgba(0, 0, 0, .25);
}

.mobile-navigation li.has-submenu.opened:after {
    border-bottom: 5px solid rgba(0, 0, 0, .25);
    border-top: none;
}
/*#endregion*/







