.discount-container-grid{
    min-height: 514px;
    grid-template-columns: repeat(auto-fit, minmax(125px, 442px));
    grid-gap: 30px;
}
.discount-container-text{
  
    &__take{
        margin-top: 60px;
    }
    &__h3{
        margin-top: -90px;
    }
    &__p{
        margin-top: -110px;
    }
    &__par{
        margin-top: -150px;
    }
}
.countdown{
    margin-top: -190px;
}
.countdown-time{
    @include font-style (30px, $fontBlack);
}


