.part3{
    margin-top: 30px;
}

.part3-grid{
    min-height: 352px;
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(288px, 330px));
    justify-content: center;
    grid-column-gap: 34px;
    grid-row-gap: 0;
    margin-bottom: -80px;
}

.img-block{
    
    width: 100%;
    height: auto;
    display: block;
    object-fit: cover;
    top: 0;
    
}
.text-block{
    display: grid;
    justify-items: center;
    grid-column-gap: 14px;
    background: $white;
    background-size: 100%;
    border: 2px solid $border;
    
    &__a{
        text-decoration: none;
        @include font-style (18px, $dark);
        text-transform: uppercase;
        margin-top: 30px;
    }
    &__p{
        @include font-style (14px, $grey);
        text-align: center;
        margin-top: 20px;
    }
    &__link{
        @include font-style (13px, $ligthBrown);
        text-transform: uppercase;
        text-decoration: none;
        border-bottom: 1px solid $ligthBrown;
        margin-top: 5px;
        margin-bottom: 20px;
    }
    &__2{
        width: 264px;
        justify-items: center;
        justify-content: center;
        border: none;
        margin-left: 20px;
        margin-top: -184px;
     }

    &__4{
       grid-column: 2;
       margin-top: -254px;
       width: 264px;
       justify-items: center;
       justify-content: center;
        border: none;
        margin-left: 20px;
    }
}

.grid-img{
    margin-top: 0px;
   
  
   &__item{
        width:100%;
        max-width: 100%;
        padding: 0;
        margin-top: 40px;
        width: 100%;
        height: auto;
        display: block;
        object-fit: cover;
       
    }
    
}

