
.img-box-grid{
    min-height: 144px;
    grid-template-columns: repeat(auto-fit, minmax(180px, 208px));
    grid-column-gap: 20px;
    cursor: pointer;

    &__title{
        @include font-style (18px, $brown);
        font-weight: 400;
        transition: 1.5s;
        transition: transform 1s linear;
    }

}

.item:hover .img-box-grid__title{
    transform: scale(1.05);
}
