.category{
    margin-top: 690px;
}

.category-grid{
    grid-template-columns: repeat(auto-fit, minmax(128px, 197px)); 
    grid-gap:  30px;

    
    &__link{
        transition: transform 0.8s ease-in;
    }
}

.category-img{
    min-height: 90px;
    width: 100px;
    margin-top: 20px;
}

.category-grid-bg{
    cursor: pointer;
    &__link{
        transition: transform 0.3s linear;
    }
}
.category-grid-bg:hover .category-grid__link{
    transform: translateY(-10px);
    cursor: pointer;
}