.category{
    margin-top: 1000px;
}
.category-desc{
    &__title{
    @include font-style (30px, $black);
    }
 }
.category-grid{
    min-height: 200px;
    grid-template-columns: repeat(auto-fit, minmax(128px, 200px)); 
    grid-gap:  30px;

    &__link{
        @include font-style (17px, $ligthBrown);
     
    }
}

.category-img{
    min-height: 90px;
    width: 100px;
}
