.footer-container{
    margin-bottom: 40px;
    max-width: 3000px;
}
.footer-grid{
    margin-top: 20px;
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(148px, 250px));
    justify-content: center;
    justify-items: center;
}
.footer-logo{
    @include font-style (28px, $black);
    letter-spacing: 2px;
    font-weight: 600;
}
.footer-nav{
    text-align: center;
  
    &__title{
        @include font-style (20px, $fontBlack);
        text-align: center;
        font-weight: 500;
        letter-spacing: 1.5px; 
        margin: 0;
        padding: 0;
    }

    &__p{
        @include font-style (16px, $lightGrey);
        justify-self: center;
        line-height: 1.5;
        margin: 0;
        padding: 20px;
        
    }

    &__list{
        list-style: none;
        line-height: 1;
        margin: 0;
        padding: 0;
        vertical-align: text-top;
        justify-items: first baseline;
        &_title{
            @include font-style (18px, $fontBlack);
            font-weight: 500;
        }
        &_link{
            text-decoration: none;
            @include font-style (16px, $lightGrey);
            justify-items: first baseline;
        }
    }
} 

.label-control{
    margin: 10px 10px auto 10px;
    justify-content: center;
}
.form-control{
    @include border ($border);
    padding: 15px 110px 15px 15px;
    font-family: $baseFontFamily;
}
.form-btn{
    display: block;
    margin-top: -70px;
    margin-left: 234px;
    margin-right: 8px;
    &__b{
        @include btn ();
        text-transform: uppercase;
        padding: 18px 20px;
    }
}
.form-wrap-btn{
    &__btn{
        opacity: 0;
    }
}
.footer-svg-box{
   display: inline-block;
    &__link{
        text-decoration: none;
        padding: 4px;
        justify-self: normal;
    }
}

.footerline-container{
    margin: 0;
    padding: 0;
    text-align: center;
    max-width: 3000px;

    &__pay-box{
      margin-left: auto;
      margin-left: 8px;
      margin-right: 8px;
        &_list{
            list-style: none;
            @include font-style (14px, $white);
            font-weight: 400;
            margin-left: 0;
            padding: 0;
            display: flex;
            margin-left: auto;
            justify-content: center;
            justify-items: center;
        }
        &_img{
            width: 36px;
            height: 22px;
            margin-left: auto;
            display: flex;
            margin: 0;
            padding: 0;
            margin-left: 6px;
        }
        &_p{
            width: 100%;
        }
        
    }
} 


