.discount-container-grid{
    min-height: 420px;
    grid-template-columns: repeat(auto-fit, minmax(125px, 360px));
    margin-top: 45px;
}

.discount-container-text{
    text-align: center;
    margin: 0;
    &__take{
        @include font-style (26px, $grey);
        margin: 0;
        margin-top: 40px;
    }
    &__h3{
        @include font-style (70px, $fontBlack);
        margin: 0;
        margin-top: -70px;
        
      
    }
    &__p{
        @include font-style (20px, $fontBlack);
        margin: 0;
        margin-top: -60px;
       
    }
    &__par{
        justify-self: center;
        margin: 0;
        margin-top: -110px;
    }
}


.countdown{
    text-align: center;
    margin-top: -110px;
}

