
.lamp-grid{
    grid-template-columns: repeat(auto-fit, minmax(280px, 444px));
}

.col-right{
    display: grid;
    grid-row-gap: 20px;
    margin-top: -35px;
   
    &__img-2{
        grid-row: 4;
    }
    
}
.col{
    &__about-text-block{
        letter-spacing: 0.5px;
        display: grid;
        text-align: center;
        padding: 0;
       
        &_title{
            @include font-style (30px, $black);
         
        }
    }
}
.col-about-btn{
    &__bttn{
        transition: 0.3s;
    }
    
    &__buttn:hover{
        cursor: pointer;
        @include btn-hover ();
        @include border ();
    }
}


    

