.img-box-container{
    padding: 0;
    margin: 0;
    max-width: 2000px;
    border-top: 1px solid $border;
    border-bottom: 1px solid $border;
}
.img-box-grid-container{
    margin: 25px;
    
}
.img-box-grid{
    display: grid;
    grid-gap: 10px;
    grid-template-columns: repeat(auto-fit, minmax(80px, 220px));
    justify-content: center;
    justify-items: center;
    

    &__img{
        max-width: 100%;
        width: 100%;
        justify-self: center;
        padding: 0;
        margin:  0;
    }

    &__title{
        @include font-style (16px, $brown);
        font-weight: 450;
        letter-spacing: 0.4px;
        text-align: center;
        padding: 0;
        margin: 0;
    }

    &__text{
        @include font-style (14px, $lightGrey);
        letter-spacing: 0.4px;
        text-align: center;
        padding: 0;
        margin: 0;
    }
}