.header-container{
    position: fixed;
    z-index: 10000px;
    background: $header;
    box-shadow: 0px 0px 25px rgba(43, 15, 64, 0.08);
    top: 0px;

}
.header-info-box{
    max-width: 100%;
    width: 100%;
    margin: 0;
    padding: 8px;
    display: flex;
    justify-content: space-between;
    justify-items:stretch;
    align-items: center;
    
    &__text, &__link, &__link2, &__list{
        display: flex;
        @include font-style (12px, $white);
        text-decoration: none;
        letter-spacing: 0.8px;
        margin: 0;
        padding: 0px;
        list-style: none;
        justify-self: center;
        align-items: center;
    }
    &__link{
        border-bottom: 1px solid $white;
        margin-left: 8px;

    }
    &__text{
        margin-left: 12px;
    }

    &__link2{
        display: flex;
       margin-left: 295px;
       margin-right: 0;
       @include font-style (12px, $white);
    
    }

    &__select-control{
        margin-left: 0px;
        margin-top: 0;
        padding: 0;
        padding: 5px;
        padding-right: 10px; 
        @include font-style (12px, $white);

        &_1{
            margin-left: 0;
            margin-top: 0;
            padding: 5px;
            padding-left: 10px; 
            margin-right: 35px;
        }
    }

    &__select-control:hover{
        cursor: pointer;
    }
}
.select-control option{
    @include font-style (16px, $dark);
}

    
.header-info-box__select-control_1:hover{
    cursor: pointer;
}
   