
.blog-slide-box{
    grid-template-columns: repeat(auto-fit, minmax(288px,400px));
    grid-gap: 30px;

    &__1, &__2, &__3, &__4, &__5, &__6{
        overflow: hidden;
    }
   
}
.slide-img{
    transition: transform 1s ease-in;
  
}

.blog-slide-box{

    &__f{
     &_link{
        transition: transform 1s ease-in;
     }
    }
}

.blog-slide-box:hover{
    cursor: pointer;
}
.blog-slide-box__1:hover  .slide-img{
    transform: scale(1.1);
    &__f:hover{
    cursor: pointer;
    }
}
.blog-slide-box__2:hover  .slide-img{
    transform: scale(1.1);
    &__f:hover{
    cursor: pointer;
    }
}
.blog-slide-box__3:hover  .slide-img{
    transform: scale(1.1);
    &__f:hover{
    cursor: pointer;
    }
}
.blog-slide-box__4:hover  .slide-img{
    transform: scale(1.1);
    &__f:hover{
    cursor: pointer;
    }
}
.blog-slide-box__5:hover  .slide-img{
    transform: scale(1.1);
    &__f:hover{
    cursor: pointer;
    }
}
.blog-slide-box__6:hover  .slide-img{
    transform: scale(1.1);
    &__f:hover{
    cursor: pointer;
    }
}
.blog-slide-box{

    &__f:hover{
    cursor: pointer;
    }
}
.blog-slide-box{

    &__f{
     &_link:hover{
        color: $brown;
     }
    }
}