.container-first{
    margin-top: 4px;
    letter-spacing: 1px;
}

.grid-container{
    min-height: 342px;
    grid-template-columns:repeat(auto-fit, minmax(125px, 174px));
    grid-column-gap: 12px;

    
    &__img {
        height: 218px;
    }
    &__p, &__par{
        @include font-style (14px, $fontBlack);
    }
   
}
