 
.form-control{
    padding: 15px 200px 15px 15px;
}
.form-btn{
    margin-left: 320px
}
.footer-nav{
    &__list{
        list-style: none;
        line-height: 1;
        margin: 0;
        padding: 0;
        vertical-align: text-top;
        justify-items: start;
        &_title{
            @include font-style (18px, $fontBlack);
            font-weight: 500;
        }
        &_link{
            text-decoration: none;
            @include font-style (16px, $lightGrey);
            justify-items: first baseline;
        }
    }
} 