.discount-container-grid{
    min-height: 330px;
    grid-template-columns: repeat(auto-fit, minmax(125px, 284px));
    margin-top: 45px;
    grid-column-gap: 20px;
    grid-row-gap: 5px;
    justify-content: center;
    justify-items: center;

    &__btn{
        &_b{
            transition: 0.3s;
        }
    }

    &__btn{
        
        &_b:hover{
            @include btn-hover ();
            @include border();
            cursor: pointer;
        }
    }
}
.item-1{
    grid-column: 1;
    grid-row: 1;
    justify-content: center;
    justify-items: center;
}
.item-2{
    grid-column: 3;
    grid-row: 1;
    justify-content: center;
    justify-items: center;
}
.item-3{
    display: grid;
    justify-content: center;
    justify-items: center;
    grid-column: 2;
    grid-row: 1;
    margin: 0;
    padding: 0;
}