.category{
    max-width: 1900px;
    padding: 0;
    margin: 0;
    margin-top: 680px;
    margin-bottom: 20px;
}
.category-desc{
   text-align: center;
  letter-spacing: 1.2px;
    &__logo{
        @include font-style (14px, $grey);
        letter-spacing: 2.8px;
        font-weight: 400;
    }
    &__title{
        @include font-style (26px, $black);
        font-weight: 400;
    }
}

.category-grid{
    min-height: 166px;
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(128px, 130px));
    grid-gap: 20px;
    justify-content: center;
    justify-items: center;
   
    &__link{
        @include font-style (14px, $ligthBrown);
        text-decoration: none;
        justify-self: center;
        text-align: center;
        margin-top: -15px;
        margin-bottom: 5px;
    }
    
}
.category-grid-bg{
    background: $pink;
    width: 100%;
    height: 100%;
}

.category-img{
    min-height: 89px;
    width: 99px;
    justify-self: center;
    justify-content: center;
    justify-items: center;
    margin-top: 10px;
}


