
.footer-grid{
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(148px, 251px));
    justify-content: center;
    justify-items: center;
}
.footer-nav{
 
    &__list{
        text-align: start;
        justify-content: start;
        justify-items: start;
        padding-left: 50px;
        &_title{
            @include font-style (18px, $fontBlack);
            font-weight: 500;
            text-align: start;
            justify-content: start;
            justify-items: start;
        }
        &_link{
            text-decoration: none;
            @include font-style (16px, $lightGrey);
            text-align: start;
            justify-content: start;
            justify-items: start;
        }
    }
} 

.list-item-1{
    display: grid;
    grid-column: 1/3;
    grid-row: 1;
}

.list-item-2{
    display: grid;
    grid-column: 1/3;
    grid-row: 2;
}


.footer-svg-box{
    display: inline-block;
     &__link{
         text-decoration: none;
         padding: 4px;
         justify-self: normal;
     }
 }

.form-control{
    padding: 15px 200px 15px 15px;
}

.form-btn{
    display: block;
    margin-top: -69px;
    margin-left: 320px;
    margin-right: 8px;
    &__b{
        @include btn ();
        text-transform: uppercase;
        padding: 18px 20px;
    }
}
