.lamp-grid{
    grid-template-columns: repeat(auto-fit, minmax(280px, 552px));

}


.col-about-btn{
    display: grid;
    justify-content: center;
    margin-top: 10px;
    
    &__buttn {
        @include btn ();
        padding: 15px 20px;
        text-transform: uppercase;
        letter-spacing: 0.5px;
        margin: 0;
    }
}
   