.slider-container{
    max-width: 2000px;
    margin: auto;
    position: relative;
    margin-top: 72px;
}

.slider__img{
    width: 100%;
    max-width: 100%;
    vertical-align: top;
    position: relative;
}

.slider-text{

    position: absolute;
    text-align: center;
    align-items: center;
    margin: 0 auto;
    top: 20px;
    margin-left: 45px;

    &__title{
        @include font-style (14px, $fontBlack);
        letter-spacing: 1.5px;
        
    }

    &__p{
        display: none;
    }

}

.slider-text-btn{
    text-align: center;
        align-items: center;
    &__btn{
        @include btn (9px);
        padding: 6px 10px;
        text-transform: uppercase;
    }
}
.myarrow_next{
    opacity: 0;
}

.myarrow_prev{
    opacity: 0;
}
.dots-box{
    margin: 0;
    padding: 0;
    list-style: none;
    margin: 1rem 0;
    display: flex;
    justify-content: center;
    justify-items: center;
    margin-top: -20px;
    
}

.dots-box > li {
    margin-right: 0.5rem;
}

.dots-box button {
    border: 2px solid $ligthBrown;
    width: 12px;
    height: 12px;
    border-radius: 12px;
    text-indent: -9999px;
    
}

.dots-box button:focus{
    outline: none;
}

.dots-box .slick-active button{
    background:$ligthBrown;
}