.part3{
    margin-top: 30px;
}

.part3-grid{
    min-height: 352px;
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(288px, 290px));
    justify-content: center;
    grid-gap: 34px;
}

.img-block{
    width: 100%;
    height: auto;
    display: block;
    object-fit: cover;
    top: 0;
    border: none;
   
}
.text-block{
    display: grid;
    justify-items: center;
    grid-column-gap: 14px;
    border: 2px solid $border;
    
    &__a{
        text-decoration: none;
        @include font-style (18px, $dark);
        text-transform: uppercase;
        margin-top: 20px;
    }
    &__p{
        @include font-style (14px, $grey);
        text-align: center;
        margin-top: 20px;
    }
    &__link{
        @include font-style (13px, $ligthBrown);
        text-transform: uppercase;
        text-decoration: none;
        border-bottom: 1px solid $ligthBrown;
        margin-top: 5px;
        margin-bottom: 20px;
    }
    &__1, &__3{
        border: none;
    }
    &__4{
        margin-top: -68px;
    }


}
.grid-img-sect{
    border-bottom: 1px solid $border;
}
.grid-img{
    min-height: 60px;
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(200px, 201px));
    grid-gap: 10px;
    justify-content: center;
    margin-top: 40px;
    @include border($border);
  
   &__item{
        width:100%;
        max-width: 100%;
        padding: 0;
        margin-top: 40px;
        width: 100%;
        height: auto;
        display: block;
        object-fit: cover;
       
    }
    
}
.part3-btn{
    margin-top: 0px;
    margin-bottom: 30px;
    text-align: center;
   
    &__btnArrow{
        @include btn ();
        padding: 4px 6px;
        margin: 0;
        font-family: $baseFontFamily;
      
        
    }
}