.part3{
    margin-top: 82px;
}

.part3-grid{
    min-height: 200px;
    grid-template-columns: repeat(auto-fit, minmax(288px, 552px));
}

.text-block{
    background: rgba($color: #fff, $alpha: 0.7);
    
    &__2{
        width: 402px;
        height: 155px;
        margin-top: -305px;
        margin-left: 30px;
    }

    &__4{
       grid-column: 2;
       margin-top: -305px;
       width: 402px;
       height: 155px;
       margin-left: 30px;
    }
}


