.latest-container{
    margin-top: 0px;
    
}
.latest-grid{
    grid-template-columns: repeat(auto-fit, minmax(288px, 455px));
    
    &__2{
        text-align: start;
        
        &_c, &_h, &_p, &_btn{
            margin-left: 20px;
        }
        &_c{
            margin-top: 25px;
        }
        &_h{
            @include font-style (30px, $black);
        }
        
    }
}
.latest-btn{
  transition: 0.3s;
}
.latest-btn:hover{
    @include btn-hover ();
    @include border();
    cursor: pointer;
}