.container-first{
    margin-top: 20px;

    &__nav{
        transition: 0.5s;
    }

    &__nav{
        &_link:hover{
            color: $black;
          border-bottom:1px solid $black;
            cursor: pointer;
            
        }
    }
   
    
}

.svg-star:hover{
    cursor: pointer;
    --fill-color: #fec42d;
   transform: scale(1.7);
}

.grid-container{
    min-height: 504px;
    grid-template-columns:repeat(auto-fit, minmax(125px, 330px));
    margin-top: 30px;
    &__img {
        height: 410px;
    }
    &__img:hover {
        cursor: pointer;
    }

    &__btn{
        transition: 0.3s;
    }
    &__btn:hover{
        cursor: pointer;
        @include btn-hover ();
        @include border();
    }
}
.button-box{
    margin-top: 22px;
   
       &__btn2{
           margin-top: 0px;
           transition: 0.3s;
       }

       &__btn2:hover{
        cursor: pointer;
        @include btn-hover ();
        @include border();
       }

       &__btn1{
           opacity: 0;
           margin: 0;
           padding: 0;
       }
}
