
.slider-text{
    top: 40px;
    margin-left: 90px;

    &__title{
        @include font-style (24px, $fontBlack); 
         
    }

    &__p{
        @include font-style (14px, $fontBlack);
        display: initial;
        text-align: center;
        
    }

}

.slider-text-btn{
   margin-top: 20px;
    &__btn{
        @include btn (13px);
        padding: 8px 10px;
        text-transform: uppercase;
    }
}