.discount-container-grid{
    max-width: 2000px;
    min-height: 150px;
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(125px, 126px));
    grid-gap: 25px;
    justify-content: center;
    justify-items: center;
    margin-top: 30px;

    &__img{
        display: grid;
        width: 100%;
        justify-self: center;
    }
    &__btn{
        text-align: center;
        margin-top: 20px;
        &_b{
            @include btn ();
            padding: 10px 20px;
            text-transform: uppercase;
        }
    }
}
.discount-container-text{
    justify-content: center;
    justify-items: center;
    letter-spacing: 0.7px;
    margin: 0;
    padding: 0;
    text-align: center;
    &__take{
        @include font-style (20px, $grey);
        font-weight: 300;
        
    }
    &__h3{
        @include font-style (50px, $fontBlack);
        font-weight: 400;
        margin: 0;
        padding: 0;
       
    }
    &__p{
        @include font-style (16px, $fontBlack);
        font-weight: 400;
        letter-spacing: 4.6px;
        margin: 0;
        padding: 0;
       
    }
    &__par{
        @include font-style (14px, $grey);
        margin-top: 15px;
        padding: 0;
    }
}
.countdown{
    text-align: center;
}
.countdown-number{
   
   text-align: center;
    padding: 12px;
    margin: 5px;
    display: inline-block;
    @include border ($border);
    margin-right: auto;
}

.countdown-time{
    @include font-style (24px, $fontBlack);
    font-weight: 400;

}
.countdown-text{
    @include font-style (14px, $grey);
}

.time{
    display: grid;
   grid-column: 1/3;
   letter-spacing: 2px;
}
.item-3{
    display: grid;
    grid-column: 1/3;
    
}

