.part3{
    margin-top: 82px;
}

.part3-grid{
    min-height: 200px;
    grid-template-columns: repeat(auto-fit, minmax(288px, 440px));
}

.img-block{
    cursor: pointer;
   
}
.text-block{
    cursor: pointer;
    background: rgba($color: #fff, $alpha: 0.7);
    &__a{
        transition: all 0.8s;
    }
    &__2{
        width: 352px;
        height: 177px;

        margin-top: -234px;
    }
    &__4{
       grid-column: 2;
       margin-top: -234px;
       width: 352px;
       height: 177px;
    }

}

.text-block:hover .text-block__a {
    transform: rotateX(1turn);
}

.grid-img{
    margin-bottom: 0px;
    border-top: 1px solid $border;
  
   &__item{
        width:100%;
        max-width: 100%;
        padding: 0;
        margin-top: 40px;
        transition: 1s;
        padding-bottom: 30px;
   }

   &__item:hover {
    cursor: pointer;
    transform: scale(0.9);
   }
    
}

.part3-btn{
    display: none;
}

   
