.slider-container{
    max-width: 2000px;
    margin: auto;
    position: relative;
    margin-top: 95px;
}
.myarrow_prev{
    left: 40px;
}

.myarrow_next{
    left: 1120px;
}


.slider-text{
    top: 160px;
    margin-left: 320px;

    &__title{
        @include font-style (43px, $fontBlack);
        margin-bottom: 20px;
    }
    &__p{
        @include font-style (16px, $fontBlack);
        margin-top: 0px;
    }
}

.slider-text-btn{
    text-align: center;
    align-items: center;
    margin-top: 20px;
   
}

