.trending-slick-container{
    padding: 0;
    margin: 0;
    margin-top: 1150px;
    max-width: 300px;
    text-align: center;
    align-items: center;
    margin-left: auto;
    margin-right: auto;
}

.trending-col-bg{
    @include border($border);
    padding:12px;
    padding-bottom: 0;
}

.trending-col{
    background: $white;
    text-align: center;
   
    &__img{
        width:100%;
        height: auto;
        text-align: center;
    }

    &__btn{
        text-align: center;
        justify-self: center;
        &_b{
            @include btn (13px);
            padding: 6px 10px;
            text-transform: uppercase;
            letter-spacing: 0.6px;
        }

    }
    &__dscr{
        text-align: center;
        letter-spacing: 0.6px;
        margin-top: 0px;
        &_p{
            @include font-style (14px, $fontBlack);
        }
        &_price{
            @include font-style (14px, $fontBlack);
            font-weight: 500;
            padding-bottom: 15px;
    
        }
        &_span{
            text-decoration: line-through;
            color: $grey;
          
        }
    }
}

.data{
    margin: 0;
    padding: 0;
    margin-top: 15px;
    display: flex;
    justify-content: center;
    justify-items: center;
}
.data-number{
    text-align: center;
    padding: 8px;
}
.data-time{
    @include font-style(16px, $brown);
}
.data-text{
    @include font-style(14px, $brown);
}
.data-span{
    margin-top: 15px;
}
.trending-svg-box{
    margin-top: 10px;
    text-align: center;
}

 .trending-desc{
    margin-top: 25px;
    text-align: center;
    letter-spacing: 0.6px;
          
        &__logo{
            @include font-style (14px, $grey);
        }
        
        &__title{
            margin-top: 0;
        @include font-style (26px, $black);
        font-weight: 400;
        }
    
}

.slick-container{
    margin: auto;
    position: relative;
   
}

.slick__item{
    padding: 8px;
}

.slick__img{
    width: 100%;
    max-width: 100%;
    vertical-align: top;
    position: relative;
    box-shadow: 0px 0px 25px rgba(112, 111, 112, 0.06);
}

.slick-svg-box{
    margin-top: 10px;
    text-align: center;
}

.slick{
    &__btn{
        margin-top: 10px;
        text-align: center;
        &_b{
            @include btn (13px);
            padding: 10px 20px;
            text-transform: uppercase;
            letter-spacing: 0.6px;
            text-align: center;
        }
    }

    &__dscr{
        text-align: center;
        letter-spacing: 0.6px;
        &_p{
            @include font-style (14px, $fontBlack);
        }
        &_price{
            @include font-style (14px, $fontBlack);
            font-weight: 500;
        }
        &_span{
            text-decoration: line-through;
            color: $grey;
        }
    }
}

.trending-btn{
    text-align: center;
    margin-top: 80px;
    margin-bottom: 20px;
    &__b{
        @include btn ();
        padding: 10px 20px;
        text-transform: uppercase;
    }
   
}

.mybtn{
    margin: 0;
    padding: 0;
    position: absolute;
    z-index: 10;
    transition: all .5s;
    text-align: center;
    align-items: center;
  
}
.mybtn_prev{
    padding: 2px 4px;
    @include btn ();
    left: 110px;
    margin-top: 371px;
    
}

.mybtn_next{
    padding: 2px 4px;
    @include btn ();
    margin-top: 15px;
    left: 150px;
    
}

.mybtn_next:hover{
    @include btn-hover ();
    @include border ();
    --fill-color: #A6866A;
}

.mybtn_prev:hover{
    @include btn-hover ();
    @include border ();
    --fill-color: #A6866A;
}