.category{
    margin-top: 870px;
}

.category-grid{
    min-height: 169px;
    grid-template-columns: repeat(auto-fit, minmax(128px, 160px)); 
    grid-gap:  15px;

    &__link{
        @include font-style (16px, $ligthBrown);
    }
}

.category-img{
    min-height: 90px;
    width: 100px;
}

